@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body{font-family:"Open Sans", sans-serif;width:100%;margin:0}body a{text-decoration:none}article{display:flex;flex-direction:row;justify-content:center}

nav{background-color:#3E424D;margin:0;display:flex;justify-content:space-between;padding:1rem 0 1rem 0}@media screen and (max-width: 900px){nav{flex-direction:column;justify-content:center}nav div:last-of-type{display:flex;justify-content:center;align-items:center}nav div:last-of-type .navItem{margin-right:0}}nav div{display:flex;justify-content:center;align-items:center}nav div h1{margin:0 0 0 30px;font-size:30px;text-transform:uppercase}@media screen and (max-width: 900px){nav div h1{margin:0}}nav div .navItem{margin-right:30px}nav div .navItem .link{font-size:20px;font-weight:500;color:#9FC5CE;letter-spacing:0.1em}nav div .navItem .link:hover,nav div .navItem .link.Mui-selected{color:#BBCB9F}@media screen and (max-width: 600px){nav div .navItem{margin:0 5px 0 5px}nav div .navItem .link{font-size:14px}}

.intro-background{background-color:#3E424D;min-height:90vh;width:100%;margin:0;display:flex;flex-direction:column;justify-content:center}@media screen and (max-width: 1000px){.intro-background{text-align:center;justify-content:flex-start}}.intro-background .container{display:flex;flex-direction:row;flex-wrap:wrap;align-items:center;justify-content:center}@media screen and (max-width: 800px){.intro-background .container{flex-direction:column;justify-content:space-between}}.intro-background .container .headshot-image{order:1;max-width:30rem}.intro-background .container .headshot-image img{max-width:20rem;padding:5px;margin:1rem;border-radius:50%;border:5px solid #BBCB9F}@media screen and (max-width: 800px){.intro-background .container .headshot-image img{width:50vw}}.intro-background .container div.intro{order:2;padding:2rem 0 5rem 2rem}@media screen and (max-width: 1000px){.intro-background .container div.intro{padding:0}}.intro-background .arrow{background-color:#3E424D;font-size:40px;color:white;width:100%}.intro-background .arrow a{text-decoration:none;text-align:center}.intro-background .arrow a i{position:relative;bottom:-12vh;left:50%;margin-left:-20px;color:white;animation:bounce 2s infinite}@media screen and (max-width: 1000px){.intro-background .arrow a i{left:2.5%;bottom:-7vh}}h1,h3,span.typewrite .greenLarge{color:#9FC5CE;font-weight:600;font-size:6vh}@media screen and (max-width: 800px){h1,h3,span.typewrite .greenLarge{font-size:5vh;text-align:center}}h3{margin-bottom:3rem;font-size:38px}@media screen and (max-width: 800px){h3{margin-bottom:0}}.green{color:#BBCB9F}Button.projects{margin-top:1rem;font-family:"Ubuntu", sans-serif;background-color:#9FC5CE}.typewrite{color:#BBCB9F;font-size:3vh}

#bio,#contact,#feedback{display:flex;flex-direction:row;justify-content:center;align-items:flex-start;padding:5rem 0}@media screen and (max-width: 600px){#bio,#contact,#feedback{padding:3rem 0}}h3.about-title,#feedback>h3{margin-top:0;color:#457b9d}p.bio-content,div.contact-info,article.feedback{font-family:"Open Sans", sans-serif;max-width:50vw;padding:0 0 0 5rem}@media screen and (max-width: 900px){p.bio-content,div.contact-info,article.feedback{max-width:80vw;padding:0}}@media screen and (max-width: 800px){#bio,#feedback,#contact{flex-direction:column;align-items:center;justify-content:center}h3.about-title{text-align:center}p.bio-content,p.feedback,div.contact-info{padding:0}}

div.skillsContainer{padding:1rem 1rem 4rem 1rem}div.skillsContainer h3{text-align:center;margin:20px 0 50px 0;color:#457b9d}@media screen and (min-width: 800px){div.skillsContainer h3 br{display:none}}div.skillsContainer div.skillsSection{display:flex;flex-direction:row;padding:0 0 5rem 0;justify-content:center}div.skillsContainer div.skillsSection:first-of-type{padding:0}div.skillsContainer div.skillsSection:first-of-type div{border-bottom:solid 5px #BBCB9F}div.skillsContainer div.skillsSection div{width:25rem;text-align:center;padding:1rem}div.skillsContainer div.skillsSection div:first-of-type{border-right:solid 5px #BBCB9F}@media screen and (max-width: 500px){div.skillsContainer div.skillsSection div:first-of-type{border-right:none}}div.skillsContainer div.skillsSection div p{font-size:20px}div.skillsContainer div.skillsSection span{font-size:50px;padding:10px;color:#9FC5CE}@media screen and (max-width: 500px){div.skillsContainer div.skillsSection{flex-direction:column;align-items:center;max-width:90vw}div.skillsContainer div.skillsSection div{width:100%;padding:0;border-bottom:solid 5px #BBCB9F}div.skillsContainer div.skillsSection div:last-of-type{border-bottom:none}}@media screen and (max-width: 500px){div.skillsContainer{display:flex;justify-content:center;flex-direction:column;align-items:center}}

#contact{background-color:#3E424D}#contact h3{text-align:right;margin-top:0;color:white}#contact div.contact-info{display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-end;font-family:"Open Sans", sans-serif;max-width:50vw}@media screen and (max-width: 900px){#contact div.contact-info{max-width:80vw}}#contact div.contact-info a.contact-item{align-items:flex-start;list-style-type:none;color:white;font-size:large;padding:.5rem;text-decoration:none}@media screen and (max-width: 300px){#contact div.contact-info a.contact-item{font-size:6vw;overflow-wrap:break-word;padding:0}}#contact div.contact-info a.contact-item:hover{color:#BBCB9F}#contact div.contact-info a.contact-item i.fas,#contact div.contact-info a.contact-item i.fab{margin:5px;font-size:larger}

#feedback{background-color:#9FC5CE}#feedback p{font-size:medium;font-weight:600;font-style:italic}

.footer{margin:0;display:flex;flex-direction:column;align-items:center;justify-content:center;height:10vh;background-color:#3E424D}.footer .btnsRow{display:flex;flex-direction:row;margin:0}.footer .btnsRow a{margin:10px;color:white;font-size:20px}.footer .btnsRow a:hover{color:#BBCB9F}@media screen and (max-width: 800px){.footer{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.footer .btnsRow>a{font-size:25px}}

article.portfolio-item div>div.btnsRow{margin-top:1rem}article.portfolio-item div>div.btnsRow .link{text-decoration:none}article.portfolio-item div>div.btnsRow .link Button.site{margin:20px}article.portfolio-item div>div.btnsRow .link Button.site:hover{background-color:#3E424D;color:white}article.portfolio-item div.image{display:flex;flex-direction:column;align-items:center;justify-content:center;margin-top:1rem;float:auto}article.portfolio-item div.image img{width:30vw;min-width:300px}@media screen and (max-width: 500px){article.portfolio-item div.image img{width:200px}}article.portfolio-item div.text{text-align:left;order:2}article.portfolio-item div.text h2{font-size:30px;font-weight:600;text-align:center;text-transform:uppercase}article.portfolio-item div.text p:first-of-type{font-size:18px}article.portfolio-item div.text p:last-of-type{font-size:18px}@media screen and (max-width: 1000px){article.portfolio-item{display:flex;flex-direction:column;margin:0}}

div.body{width:100vw;background-color:#3E424D}header{text-align:center;display:flex;justify-content:center}header h1{align-self:flex-end;text-align:center;color:#9fc5ce}article.portfolio-item{padding:0 2rem;display:flex;margin:2vh 10vw;border-radius:5px}article.portfolio-item:nth-of-type(odd){flex-direction:row-reverse;text-align:right;background-color:rgba(159,197,206,0.5)}article.portfolio-item:nth-of-type(odd) h2{text-align:center}article.portfolio-item:nth-of-type(odd) div.text{padding-right:2vw}article.portfolio-item:nth-of-type(odd) Button.site{background-color:#BBCB9F}article.portfolio-item:nth-of-type(even){background-color:rgba(187,203,159,0.5)}article.portfolio-item:nth-of-type(even) div.text{padding-left:2vw}article.portfolio-item:nth-of-type(even) Button.site{background-color:#9FC5CE}article.portfolio-item:last-of-type{margin-bottom:0}@media screen and (max-width: 900px){div.body{background-color:white}div.body h1{color:#3E424D}article.portfolio-item{display:flex;flex-direction:column}article.portfolio-item h1,article.portfolio-item h2{text-align:center}article.portfolio-item:nth-of-type(odd){flex-direction:column}article.portfolio-item:nth-of-type(odd) div.text{padding-right:0}article.portfolio-item:nth-of-type(even) div.text{padding-left:0}}@media screen and (max-width: 500px){article.portfolio-item{border-radius:0;margin:0}}

